
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import applyItem from 'components/apply/apply-item.vue';
import baseAdd from 'components/base-add.vue';
import QuickMenu from 'components/quick-menu.vue';
import {applyQuery, applyCreate, deleteAll} from '@/api/apply-detail'

export default {
name: 'apply',
components: {
Search,
Tab,
TabItem,
applyItem,
baseAdd,
QuickMenu,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
data() {
return {
applyQueryList: [],//
applyCreateList: [],//
isAddApply: true,
applyTypeList: [
{key: 'vacationInfo', value: ''},
{key: 'applyHoliday', value: ''},
{key: 'applyOvertime', value: ''},
{key: 'applySupplySign', value: ''}
],//
applyStatus: "1",//1 2 3
isQuickMenuFlag: false,
selectedOne: true,
selectedTwo: false,
selectedThree: false,
searchValue: '',
createList: {},
}
},
methods: {
startMove(){
console.log('startMove');
this.$nextTick(() => {
this.$refs.scroller.reset()
})
},
endMove(){
console.log('endMove');
this.$nextTick(() => {
this.$refs.scroller.reset()
})
},
changeQuickMenuFlag (){
this.isQuickMenuFlag = !this.isQuickMenuFlag;
},
onItemClick(index) {//
this.isLoadingData = true;
switch (index) {
case 0:
this.applyStatus = "1";
this.getQueryList("1");
this.isAddApply = true;
this.isQuickMenuFlag = false;
this.selectedOne = true;
this.selectedTwo = false;
this.selectedThree = false;
break;
case 1:
this.applyStatus = "2";
this.getQueryList("2");
this.isAddApply = false;
this.selectedOne = false;
this.selectedTwo = true;
this.selectedThree = false;
break;
case 2:
this.applyStatus = "3";
this.getQueryList("3");
this.isAddApply = false;
this.selectedOne = false;
this.selectedTwo = false;
this.selectedThree = true;
break;
default:
break;
}
console.log("applyStatus:", this.applyStatus);
window.localStorage.setItem('applyStatus', this.applyStatus);
},

goDetail(item){
console.log("", JSON.stringify(item));
this.$router.push({name: item.hrWorkflowType, query: {"item": item, applyStatus: this.applyStatus}});
},

onButtonClick(item, index){
console.log(":", JSON.stringify(index));
deleteAll({"hrWorkflowId": item.hrWorkflowId}).then(response => {
if (response.data && response.data.success) {
this.$vux.toast.show({text: ''});
this.applyQueryList.splice(index, 1)
} else {
this.$vux.toast.show({text: ''});
}
}).catch(error => {
this.$vux.toast.show({text: ''});
});
},

create(item){
console.log("addApply", JSON.stringify(item));
this.$router.push('/' + item.key);
},

getQueryList(type){
applyQuery({"hrWorkflowNodeStatus": this.applyStatus}).then(response => {
if (response.data && response.data.rows) {
this.applyQueryList = response.data.rows;
this.isLoadingData = false;
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
});
},
},
mounted() {

if (window.localStorage.getItem('applyStatus') === '3') {
this.applyStatus = "3";
this.selectedOne = false;
this.selectedTwo = false;
this.selectedThree = true;
} else if (window.localStorage.getItem('applyStatus') === '2') {
this.applyStatus = "2";
this.selectedOne = false;
this.selectedTwo = true;
this.selectedThree = false;
} else {
this.applyStatus = "1";
this.selectedOne = true;
this.selectedTwo = false;
this.selectedThree = false;
}
if (window.localStorage.getItem('applyStatus')) {
this.getQueryList(window.localStorage.getItem('applyStatus'));
} else {
this.getQueryList("1");
}
},
}



